import React  from 'react'
import axios from 'axios'; 
import {
  CButton,
  CCardBody,
  CForm,
  CInputGroup,
  CRow,
  CInput
} from '@coreui/react'
import { Redirect, Route, Switch } from 'react-router-dom';
import {loginurl, loginUrl} from '../Config'

const TheLayout = React.lazy(() => import('../../containers/TheLayout'));

class Loginsection extends React.Component {
    state ={
        data:null
    }
    handleChange =(event)=>{
        const name = event.target.name;
        const value = event.target.value;
        this.setState({
            [name]: value
        })
    }



    handleLogin = (event)=>{
        event.preventDefault();

        const loginRequest = {
          "email": this.state.email,
          "password": this.state.password
      }

axios.post(loginUrl, loginRequest)
    .then(data => {
      localStorage.setItem('token', data.data.key);
      window.location.reload();
    })    
    .catch(error => console.log("Error detected: " + error))

    }
render() {
  return  <>
    <CCardBody>
        <CForm>          
          <CInputGroup className="mb-3">
          </CInputGroup>
          <CInputGroup className="mb-3">
            <CInput type="text" placeholder="Email" autoComplete="email" name="email" onChange={this.handleChange}  />
          </CInputGroup>
          <CInputGroup className="mb-4">
            <CInput type="password" placeholder="Password" autoComplete="current-password"  name="password" onChange={this.handleChange}  />
          </CInputGroup>
          <CRow>
            <CButton color="info" size="lg" block style={{font:'poppins'}} onClick={this.handleLogin} ><strong>Login</strong></CButton>
          </CRow>
        </CForm>

      <p className="text-primary">Forget your password ?</p>
    <br/>
      </CCardBody>

</>;
      }

}

export default  Loginsection

