import React, { useState } from 'react'
import {
  CButton,
  CCardBody,
  CForm,
  CInputGroup,
  CRow,
  CInput
} from '@coreui/react'

class Signinsection extends React.Component {
  handleChange =(event)=>{
    const name = event.target.name;
    const value = event.target.value;
    this.setState({
        [name]: value
    })
}


handleSignin = (event)=>{
    event.preventDefault();
    console.log(this.state);

console.log(JSON.stringify(this.state));
// var token = '3bf64d3cf75edb0489bd32989f7abe2d2c8ad094';


  fetch('http://127.0.0.1:8000/api/v1/auth/rest-auth/registration/teacher', {
    method: 'POST',
    headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json;charset=UTF-8'
    },
    mode: 'cors',
    body: JSON.stringify(this.state),
}).then(response => response.text())
    .then(data => console.log(data))    
    .catch(error => console.log("Error detected: " + error))
}
render() { 
        return  <>
              <CCardBody>
                  <CForm>
                            
                    <CInputGroup className="mb-3">
                      <CInput type="text" placeholder="Email " name="email" onChange={this.handleChange}/>
                    </CInputGroup>
                    <CInputGroup className="mb-3">
                      <CInput type="password" placeholder="password" autoComplete="username" name="password1" onChange={this.handleChange} />
                    </CInputGroup>
                    <CInputGroup className="mb-4">
                      <CInput type="password" placeholder="confirm Password" autoComplete="current-password" name="password2" onChange={this.handleChange} />
                    </CInputGroup>
                    <CRow>
                    <CButton color="info" size="lg" block style={{font:'poppins'}} onClick={this.handleSignin} ><strong>Register</strong></CButton>
                    <CButton variant="ghost" color="danger" size="lg" block style={{color:'#005688',font:'poppins'}} > <strong>Cancel</strong></CButton>
                    </CRow>
                  </CForm>
                </CCardBody>
      
          </>;
      }

}

export default Signinsection
